import Emitter from './Emitter';

class TouchGesture {
  constructor(el) {
    // if (new.target !== TouchGesture) {
    //   throw Error('class TouchGesture must be used by the new operator');
    // }
    this.name = 'TouchGesture';

    this.emitter = null;

    this.element = typeof el === 'string' ? document.querySelector(el) : el;

    /** 起始点坐标 */
    this.startX = null;
    this.startY = null;
    /**
     * 更新touchmove 过程坐标,计算位移
     */
    this.moveX = null;
    this.moveY = null;

    this.startTime = null;

    this.thresholdVal = 10;

    this.swipeDirection = '';

    // 控制 end 事件是否需要阻止默认事件传递，
    // 否则子元素绑定了touchend会在滑动结束触发事件, 备用
    this.isMove = false;
    this.bindEvents();
  }
  /**
   * 注册自定义逻辑函数，提供钩子
   * @param {*} type
   * @param {*} callback
   */
  on(type, callback = () => {}) {
    this.emitter.on(type, callback);
  }
  emit(type, ...rest) {
    this.emitter.emit(type, ...rest);
  }
  /**
   *
   * @param {*} e
   */
  _touchStart(e) {
    this.isMove = false;
    e && e.preventDefault();
    e && e.stopPropagation();

    const point = e.touches ? e.touches[0] : e;
    this.startX = point.pageX;
    this.startY = point.pageY;

    this.moveX = this.startX;
    this.moveY = this.startY;

    this.startTime = Date.now();

    this.emitter.emit('touchstart', {
      startPoint: point,
      startTime: this.startTime,
    });
  }
  /**
   *
   * @param {*} e
   */
  _touchMove(e) {
    const point = e.touches ? e.touches[0] : e;
    this.isMove = true;
    e && e.preventDefault();
    e && e.stopPropagation();
    /**
     * 忽略微小的触摸滑动
     */
    if (
      Math.abs(point.pageX - this.startX) < this.thresholdVal &&
      Math.abs(point.pageY - this.startY) < this.thresholdVal
    ) {
      return;
    }
    this.emitter.emit('touchmove', {
      differenceX: point.pageX - this.startX,
      differenceY: point.pageY - this.startY,
      distX: point.pageX - this.moveX,
      distY: point.pageY - this.moveY,
      movePoint: point,
      moveTime: Date.now(),
    });

    this.moveX = point.pageX;
    this.moveY = point.pageY;
  }
  /**
   *
   * @param {*} e
   */
  _touchEnd(e) {
    if (this.isMove) {
      e && e.preventDefault();
      e && e.stopPropagation();
    }
    const endTime = Date.now();
    const point = e.changedTouches ? e.changedTouches[0] : e;
    const distX = point.pageX - this.startX;
    const distY = point.pageY - this.startY;
    const absDistX = Math.abs(distX);
    const absDistY = Math.abs(distY);
    if (absDistX > this.thresholdVal || absDistY > this.thresholdVal) {
      if (absDistX >= absDistY) {
        this.swipeDirection = distX < 0 ? 'Left' : 'Right';
      } else {
        this.swipeDirection = distY < 0 ? 'Up' : 'Down';
      }
      const _data = {
        swipeDirection: this.swipeDirection,
        startPoint: {
          x: this.startX,
          y: this.startY,
        },
        endPoint: {
          x: point.pageX,
          y: point.pageY,
        },
        timeCost: endTime - this.startTime,
        endTime,
      };
      this.emitter.emit(`swipe${this.swipeDirection}`, _data);
      this.emitter.emit('swipe', _data);
      this.emitter.emit('touchend', _data);
    } else {
      this.emitter.emit('tap');
    }
  }
  /**
   *
   * @param {*} e
   */
  _touchCancel(e) {
    this._touchEnd(e);
  }
  handleEvent(e) {
    switch (e.type) {
      case 'touchstart':
        this._touchStart(e);
        break;
      case 'touchmove':
        this._touchMove(e);
        break;
      case 'touchend':
        this._touchEnd(e);
        break;
      default:
        console.log('发现未捕获事件');
        break;
    }
  }
  /**
   * 绑定事件
   */
  bindEvents() {
    if (!this.element) {
      return;
    }
    const _touchEvents = {
      touchstart: { capture: false },
      touchmove: { capture: false },
      touchend: { capture: true },
      touchcancel: { capture: false },
    };
    Object.keys(_touchEvents).forEach(type => {
      this.element.addEventListener(type, this, _touchEvents[type].capture);
    });

    if (!this.emitter) this.emitter = new Emitter();
  }
}
export default TouchGesture;
